import React from 'react';
import { Component } from 'react';

class Counter extends Component {
  constructor(props){
      super(props);
      this.state={count:0}
  }

    onClickHandlerPlus = async() => {

      let plusCount = this.state.count + 1;

      await this.setState({ count: plusCount });
      this.props.handleTotalApp(plusCount);
    }
    onClickHandlerMoin = async () => {

      let moinCount = this.state.count- 1;

      await  this.setState({ count: moinCount });
      this.props.handleTotalApp(moinCount);
    }

    onClickHandlerReset = async () => {

      let Count = 0;

      await  this.setState({ count: Count });
      this.props.handleTotalApp(Count);
    }
  
  

  render() {
    return (
      <div>
 


        <div className="btn-group btn-group-lg" role="group" aria-label="buton"> 
         <span id="numberCounter" class="display-3">
            {this.state.count}
          </span>
          <button type="button" onClick={this.onClickHandlerPlus} id="plusButton" className="btn btn-danger m-2">+</button>
          <button type="button" onClick={this.onClickHandlerMoin} id="moinButton" className="btn btn-warning m-2">-</button>
          <button type="button" onClick={this.onClickHandlerReset} id="resetButton" className="btn btn-secondary m-2">reset</button>

        </div>

      </div >

    );
  }
}

export default Counter;
