import React from 'react';
import Counter from './Counter';
import Navbar from './Navbar'
import { Component } from 'react';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = { counters:[0,0,0],total: 0 };
      }
    
      handleTotalAppInner=(count,i)=>{
    
        console.log("dd",count,i);
        let counters=this.state.counters;
        counters[i]=count;
    
        this.setState({counters});
        let totalcount=counters.reduce((accumulateur,valeurcourante) => accumulateur+valeurcourante,0)
        this.setState({ total:  totalcount })
    
      }
    
      render() {
        return (
          <div className="Main">
            <div >
    
              <Navbar total={this.state.total} />
              {
             this.state.counters.map( (counter,i) => {
             return <Counter key={i} handleTotalApp={(valeur)=>this.handleTotalAppInner(valeur,i)}  />
             })
            }
            </div>
          </div>
        );
      }
    }

export default Main;

